import {
  Link
} from "react-router-dom";

function Sidebar() {
  return (
    <section class="d-flex flex-column flex-shrink-0 p-3 text-white bg-dark sidebar">
      <div class="text-center">
        <Link to="/" className="d-block text-white p-2">About</Link>
        <Link to="/experiences" className="d-block text-white p-2">Experiences</Link>
        <Link to="/contact-me" className="d-block text-white p-2">Contact Me</Link>
      </div>
    </section>
  )
}

export default Sidebar;